export default {
	data() {
		return {
			defaultImg:'/static/img/header-img.png',
			status: [
				{
					name: '我的订单',
					icon: '/static/fumou-center-template/one.png',
					url: 'order'
				},
				{
					name: '报事工单',
					icon: '/static/fumou-center-template/2.png',
					url: 'repair-order'
				},
				{
					name: '发票下载',
					icon: '/static/fumou-center-template/3.png',
					url: 'electronic_invoice'
				},
				{
					name: '我的余额',
					icon: '/static/fumou-center-template/4.png',
					url: 'charge-money'
				}
			],
			menus: [
				{
					name: '我的房产',
					icon: '/static/fumou-center-template/5.png',
					url: 'my-house'
				},
				{
					name: '我的车辆',
					icon: '/static/fumou-center-template/6.png',
					url: 'my-car'
				},
				{
					name: '报事工单',
					icon: '/static/fumou-center-template/8.png',
					url: 'repair-order'
				},
				{
					name: '联系物业',
					icon: '/static/fumou-center-template/10.png',
					url: 'call'
				},
				{
					name: '关于我们',
					icon: '/static/fumou-center-template/10.png',
					url: 'about'
				}
			],
			ownerInfo: ''
		};
	},
	onShow() {
		// this.getData();
	},
	methods: {
		getData() {
			let data = {};
		},
		logout() {
			uni.showModal({
				title: '是否退出登陆？',
				content: '退出登陆将无法收到消息,您真的要退出登陆吗？',
				success: res => {
					if (res.confirm) {
						setTimeout(() => {
							this.$store.commit('logout');
							// this.$Router.replaceAll({name:'login'})
						}, 100);
					}
				}
			});
		},
		getNav(url) {
			if (!url) {
				uni.showToast({
					icon: 'none',
					title: '正在开发...'
				});
				return;
			}
			this.$Router.push({ name: url });
		}
	},
	computed: {
		userInfo() {
			let userInfo = {};
			try {
				userInfo = uni.getStorageSync('userInfo');
				return userInfo;
			} catch (e) {
				// error
			}
			return userInfo;
		},
		myHouse() {
			return this.$store.state.myHouse;
		},
		hasBoundHouse(){
			return this.$store.state.hasBoundHouse;
		},
		hasLogin(){
			return this.$store.state.hasLogin;
		}
	}
};
